/* ==========
NAVIGATION
========== */

// app menu button

.app-nav-btn {
    position: fixed;
    top: .4rem;
    right: 8.4rem;
    z-index: 999999;
    width: 6rem;
    height: 6rem;
    border-radius: 50%;
    padding: 0;
    margin: 0;
    outline: none;
    border: none;
    background-color: #fff;
    cursor: pointer;
    -webkit-appearence: none;
    -webkit-tap-highlight-color: transparent;

    svg {
        position: absolute;
        top: 1.2rem;
        right: 0;
        stroke: black;
        stroke-width: 4px;
        stroke-linecap: round;
        stroke-linejoin: round;
        fill: none;
        display: block;
        position: absolute;

        path {
            transition: stroke-dasharray var(--duration, .85s) var(--easing, ease) var(--delay, 0s), stroke-dashoffset var(--duration, .85s) var(--easing, ease) var(--delay, 0s);
            stroke-dasharray: var(--array-1, 26px) var(--array-2, 100px);
            stroke-dashoffset: var(--offset, 126px);
            transform: translateZ(0);

            &:nth-child(2) {
                --duration: .7s;
                --easing: ease-in;
                --offset: 100px;
                --array-2: 74px;
            }

            &:nth-child(3) {
                --offset: 133px;
                --array-2: 107px;
            }
        }
    }

    &.active {
        svg {
            path {
                --offset: 57px;

                &:nth-child(1),
                &:nth-child(3) {
                    --delay: .15s;
                    --easing: cubic-bezier(.2, .4, .2, 1.1);
                }

                &:nth-child(2) {
                    --duration: .4s;
                    --offset: 2px;
                    --array-1: 1px;
                }

                &:nth-child(3) {
                    --offset: 58px;
                }
            }
        }
    }
}


.app-nav-modal {

    .modal-dialog {
        margin-top: 8rem;
    }

    .modal-content {
        border-style: none;
        border-radius: .4rem;
        box-shadow: 2px 2px 12px 12px rgba(00, 00, 00, .1);
    }

    .nav {
        width: 100%;
    }

    .list {
        padding: 4rem 0;
        width: 100%;
        list-style: none;
    }

    .list-item {
        margin-top: .8rem;
    }

    .link {
        display: block;
        font-size: 2.4rem;
        font-weight: 600;
        line-height: 1.1;
        padding: 1.2rem 2.4rem 1.2rem 4rem;
        position: relative;

        &:before {
            @include transition02;
            transform: scaleX(0);
            transform-origin: top left;
            position: absolute;
            top: 0;
            left: 0;
            width: 1.6rem;
            height: 100%;
            background-color: blue;
            content: '';
        }
    }

    .btn-close {
        border-style: none;
        background-color: transparent;
    }
}