/* ==========
APP HEADER
========== */

.app-header {
    min-height: 6.4rem;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    z-index: 1;
    background-color: #fff;
    padding: 2rem;
}

.app-name {
    font-size: 1.6rem;
    text-transform: uppercase;
    font-weight: 300;
    letter-spacing: 1rem;
    text-align: center;
    padding: 0;
    margin: 0;

    a:hover {
        color: #186ddb;
    }

    span:first-child {
        &:after {
            content: '//';
            margin: 0 2.4rem;
        }
    }
}